.contact-info li {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  /*quiz: RWD時會做變更，是要設在HTML好還是CSS裡?*/ }
  @media (max-width: 768px) {
    .contact-info li {
      margin-bottom: 8px; } }
  .contact-info li label {
    padding-bottom: 4px;
    line-height: 24px; }
    @media (max-width: 768px) {
      .contact-info li label {
        font-size: 14px; } }
  .contact-info li input {
    height: 50px;
    font-size: 16px; }
    @media (max-width: 768px) {
      .contact-info li input {
        height: 36px; } }
  .contact-info li textarea {
    height: 136px;
    font-size: 16px; }
    @media (max-width: 768px) {
      .contact-info li textarea {
        height: 100px; } }

.contact-info p {
  display: flex;
  justify-content: center;
  font-size: 16px; }
  @media (max-width: 768px) {
    .contact-info p label {
      font-size: 14px;
      text-align: left;
      line-height: 21px; } }
  .contact-info p button {
    width: 48%;
    height: 54px;
    font-size: 16px;
    background-color: #AA0601;
    color: #ffff; }
    @media (max-width: 768px) {
      .contact-info p button {
        width: 45%;
        height: 40px; } }
    @media (max-width: 375px) {
      .contact-info p button {
        width: 48%;
        height: 36px; } }

